import { ProductLine } from "./ProductLine";
import { Card, CloseButton, Collapse } from "react-bootstrap";
import { useState } from "react";


export function Category(props) {
    const {category} = props;
    const [open, setOpen] = useState(false);
    return <>
        <Card style={{width: '18rem'}}>
            <Card.Header style={{color: 'white', backgroundColor: 'black'}} onClick={() => setOpen(!open)}>
                {category.name}{open && <CloseButton variant="white" style={{float: 'right'}}>

            </CloseButton>}

            </Card.Header>
            <Collapse in={open}>
                <Card.Body>
                    {category.products.map((p) => <ProductLine product={p} key={p.name}/>)}
                </Card.Body>

            </Collapse>


        </Card>


    </>;
}