import { Card, Col, Row } from "react-bootstrap";

function ProductSize(props) {
    const {product} = props;
    if (!product.size) return null;
    return <span className="productSize"> - {product.size}cl</span>;
}

function ProductNote(props) {
    const {product} = props;
    return <div className="note">{product.note}</div>;
}

export function ProductLine(props) {
    const {product} = props;
    return <Card style={{width: '30rem'}}>

        <Row>
            <Col>{product.name}<ProductSize product={product}/></Col>
            <Col>{product.price}&euro;</Col>
        </Row>
        <ProductNote product={product}/>
    </Card>
}